import React from "react";
import { Typography, Grid } from "@material-ui/core";
import { ResultTitle } from "./ResultTitle";
import BMIResultIcon from "./BMIResultIcon.svg";
import BMIInfoGraphRail from "./BMIInfoGraphRail.svg";
import "./BMIResult.css";
import thumbNormal from "./weightRangeInfoGraphThumbNormal.svg";
import thumbUnder from "./weightRangeInfoGraphThumbUnder.svg";
import thumbOver from "./weightRangeInfoGraphThumbOver.svg";
import thumbOverweight from "./weightRangeInfoGraphThumbOverweight.svg";

export default function BMIResult(props) {
  let weight = parseFloat(props.userData.weight);
  let height = parseFloat(props.userData.height) / 100;
  let BMI = parseFloat((weight / (height * height)).toFixed(1));

  let moveUnit =
    BMI < 13
      ? -850
      : BMI < 25
      ? (BMI - 25) * 69.54
      : BMI < 34.4
      ? (BMI - 25) * 90.4
      : 850;

  return (
    <>
      <ResultTitle>
        <img src={BMIResultIcon} alt="" />
        BMI
      </ResultTitle>
      <div className="BMIResultTypography">
        <Typography
          variant="h3"
          className={`BMI-color-${
            BMI < 18.5
              ? "under"
              : BMI < 25
              ? "normal"
              : BMI < 30
              ? "over"
              : "obese"
          }`}
        >
          {BMI}
        </Typography>
        <Typography variant="h6">Body Mass Index</Typography>
      </div>
      <div className="BMI-rail-container">
        <Typography className="BMI-mark BMI-mark-first">18.5</Typography>
        <Typography className="BMI-mark BMI-mark-second">25</Typography>
        <Typography className="BMI-mark BMI-mark-third">30</Typography>
        <img src={BMIInfoGraphRail} alt="BMI infograph" className="BMI-rail" />
        <img
          src={
            BMI < 18.5
              ? thumbUnder
              : BMI < 25
              ? thumbNormal
              : BMI < 30
              ? thumbOverweight
              : thumbOver
          }
          alt=""
          className="BMI-rail-thumb"
          style={{ transform: `translateX(${moveUnit}%)` }}
        />
      </div>
      <Grid container>
        <Grid item xs>
          <Typography className="BMI-label BMI-color-under">
            Underweight
          </Typography>
        </Grid>
        <Grid item xs>
          <Typography className="BMI-label BMI-color-normal">Normal</Typography>
        </Grid>
        <Grid item xs>
          <Typography className="BMI-label BMI-color-over">
            Overweight
          </Typography>
        </Grid>
        <Grid item xs>
          <Typography className="BMI-label BMI-color-obese">Obese</Typography>
        </Grid>
      </Grid>
    </>
  );
}
