import React, { useRef } from "react";
import "./ResultsPanel.css";
import { Grid, Container, Button } from "@material-ui/core";
import { CSSTransition } from "react-transition-group";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ResultsSection from "./ResultsSection";
import WeightResult from "./WeightResult";
import WeightRange from "./WeightRange";
import BMIResult from "./BMIResult";
import CalorieExpenditure from "./CalorieExpenditure";

export default function ResultsPanel(props) {
  const transitionRef = useRef(null);
  return (
    <CSSTransition
      in={props.showResults}
      timeout={600}
      classNames="panel-in"
      unmountOnExit
      appear
      nodeRef={transitionRef}
    >
      <Grid
        className="ResultsPanel"
        container
        direction="column"
        justify="center"
        alignItems="center"
        ref={transitionRef}
      >
        <Button
          className="results-panel-back-button"
          color="secondary"
          startIcon={<ArrowBackIosIcon />}
          onClick={() => {
            props.setShowResults();
          }}
        >
          Back
        </Button>

        <Container maxWidth="md" className="ResultsPanel-container">
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="flex-start"
          >
            <Grid item container sm={6}>
              <ResultsSection>
                <WeightResult userData={props.userData} />
              </ResultsSection>
            </Grid>

            <Grid item container sm={6}>
              <ResultsSection>
                <WeightRange userData={props.userData} />
              </ResultsSection>
            </Grid>

            <Grid item container sm={6}>
              <ResultsSection>
                <BMIResult userData={props.userData} />
              </ResultsSection>
            </Grid>

            <Grid item container sm={6}>
              <ResultsSection>
                <CalorieExpenditure userData={props.userData} />
              </ResultsSection>
            </Grid>
          </Grid>
        </Container>

        <ResultsSection>
          <Button
            variant="contained"
            color="secondary"
            size="large"
            fullWidth
            className="results-panel-action-button"
          >
            Take a Meal Plan
          </Button>
        </ResultsSection>
      </Grid>
    </CSSTransition>
  );
}
