import React from "react";
import "./WeightRange.css";
import { Typography } from "@material-ui/core";
import { ResultTitle } from "./ResultTitle";
import weightRangeIcon from "./weightRangeIcon.svg";
import weightRangeInfoGraphRail from "./weightRangeInfoGraphRail.svg";
import thumbNormal from "./weightRangeInfoGraphThumbNormal.svg";
import thumbUnder from "./weightRangeInfoGraphThumbUnder.svg";
import thumbOver from "./weightRangeInfoGraphThumbOver.svg";

export default function WeightRange(props) {
  let weight = parseFloat(props.userData.weight);
  let height = parseFloat(props.userData.height) / 100;
  let minWeight = parseFloat((height * height * 18.5).toFixed(1));
  let maxWeight = parseFloat((height * height * 25).toFixed(1));

  let moveUnit =
    ((weight - (minWeight + maxWeight) / 2) * 850) / (maxWeight - minWeight);

  return (
    <>
      <ResultTitle>
        <img src={weightRangeIcon} alt="" />
        Normal Weight Range
      </ResultTitle>
      <div className="weight-range-infographic-container">
        <Typography variant="h6" className="weight-range-mark weight-range-min">
          {minWeight}kg
          <br />
          <Typography>min</Typography>
        </Typography>
        <Typography variant="h6" className="weight-range-mark weight-range-max">
          {maxWeight}kg
          <br />
          <Typography>max</Typography>
        </Typography>
        <div className="weight-range-rail-container">
          <img
            src={weightRangeInfoGraphRail}
            alt="weight range"
            className="weight-range-rail"
          />
          <div
            className="weight-range-rail-thumb-container"
            style={{
              transform: `translateX(${
                moveUnit < -850 ? -850 : moveUnit > 850 ? 850 : moveUnit
              }%)`,
            }}
          >
            <img
              src={
                weight < minWeight
                  ? thumbUnder
                  : weight > maxWeight
                  ? thumbOver
                  : thumbNormal
              }
              alt=""
              className="weight-range-rail-thumb"
            />
            <Typography
              variant="caption"
              className={`weight-range-rail-thumb-label weight-range-rail-thumb-label-${
                weight < minWeight
                  ? "under"
                  : weight > maxWeight
                  ? "over"
                  : "normal"
              }`}
            >
              current
            </Typography>
          </div>
        </div>
      </div>
    </>
  );
}
