import React from "react";
import { Typography, Grid } from "@material-ui/core";
import { ResultTitle } from "./ResultTitle";
import weightResultIcon from "./weightResultIcon.svg";
import weightResultInfoGraphNormal from "./weightResultInfoGraphNormal.svg";
import weightResultInfoGraphUnder from "./weightResultInfoGraphUnder.svg";
import weightResultInfoGraphOver from "./weightResultInfoGraphOver.svg";
import "./WeightResult.css";

export default function WeightResult(props) {
  function getWeightResult() {
    let weight = parseFloat(props.userData.weight);
    let height = parseFloat(props.userData.height) / 100;
    let BMI = weight / (height * height);
    switch (true) {
      case BMI < 18.5:
        return "under";
      case BMI >= 18.5 && BMI < 25:
        return "normal";
      case BMI >= 25 && BMI < 30:
        return "over";
      case BMI > 30:
        return "obese";
      default:
        console.log("ERROR: none match");
    }
  }

  let gotResult = getWeightResult();

  return (
    <>
      <ResultTitle>
        <img src={weightResultIcon} alt="" />
        Weight
      </ResultTitle>
      <div className="weight-result-infographic-container">
        <img
          src={
            gotResult === "under"
              ? weightResultInfoGraphUnder
              : gotResult === "normal"
              ? weightResultInfoGraphNormal
              : weightResultInfoGraphOver
          }
          alt="weight infographic"
        />
        <Grid
          className="weight-typography"
          container
          alignItems="baseline"
          spacing={1}
        >
          <Grid item xs />
          <Grid item xs>
            <Typography component="h5" variant="h2">
              {props.userData.weight}
            </Typography>
          </Grid>
          <Grid item xs>
            <Typography component="h5" variant="h5">
              kg
            </Typography>
          </Grid>
        </Grid>
      </div>
      <Typography
        className={`weight-result-typography weight-result-typography-${
          gotResult === "under"
            ? "under"
            : gotResult === "normal"
            ? "normal"
            : "over"
        }`}
        component="h5"
        variant="h5"
      >
        {gotResult === "under"
          ? "Underweight"
          : gotResult === "normal"
          ? "Normal"
          : gotResult === "over"
          ? "Overweight"
          : "Obese"}
      </Typography>
    </>
  );
}
