import React from "react";
import { Typography, Grid } from "@material-ui/core";
import { ResultTitle } from "./ResultTitle";
import meter from "./calorieExpenditureMeter.svg";
import icon from "./calorieExpenditureIcon.svg";
import "./CalorieExpenditure.css";

export default function CalorieExpenditure(props) {
  let age = parseFloat(props.userData.age);
  let weight = parseFloat(props.userData.weight);
  let height = parseFloat(props.userData.height);
  let BMIheight = parseFloat(props.userData.height) / 100;
  let BMI = parseFloat((weight / (BMIheight * BMIheight)).toFixed(1));
  let activity = props.userData.activity;
  let activityFactor =
    activity === "never"
      ? 1.2
      : activity === "1 - 3 days"
      ? 1.375
      : activity === "4 - 5 days"
      ? 1.55
      : activity === "6 - 7 days"
      ? 1.725
      : "error";

  let caloriesToMaintain =
    props.userData.gender === "male"
      ? parseFloat(
          (
            (66.5 + 13.75 * weight + 5 * height - 6.76 * age) *
            activityFactor
          ).toFixed()
        )
      : props.userData.gender === "female"
      ? parseFloat(
          (
            (655.1 + 9.56 * weight + 1.85 * height - 4.67 * age) *
            activityFactor
          ).toFixed()
        )
      : "error";

  return (
    <>
      <ResultTitle>
        <img src={icon} alt="" />
        Calorie Intake
      </ResultTitle>
      <Grid container className="calorie-expenditure-inforgraphy-container">
        <Grid
          item
          xs
          container
          direction="column"
          justify="space-around"
          alignItems="center"
          className="calorie-expenditure-labels-container"
        >
          <Typography
            variant={BMI < 18.5 ? "h5" : "subtitle1"}
            className={`calorie-expenditure-meter-label calorie-expenditure-top ${
              BMI < 18.5 && "calorie-expenditure-color-under"
            }`}
          >
            {BMI > 25 ? "Gain Weight" : "Gain Muscle"}
          </Typography>
          <Typography
            variant={BMI >= 18.5 && BMI <= 25 ? "h5" : "subtitle1"}
            className={`calorie-expenditure-meter-label calorie-expenditure-middle ${
              BMI >= 18.5 && BMI <= 25 && "calorie-expenditure-color-normal"
            }`}
          >
            Maintain Weight
          </Typography>
          <Typography
            variant={BMI > 25 ? "h5" : "subtitle1"}
            className={`calorie-expenditure-meter-label calorie-expenditure-bottom ${
              BMI > 25 && "calorie-expenditure-color-over"
            }`}
          >
            Burn Fat
          </Typography>
        </Grid>
        <Grid item xs={1} className="calorie-expenditure-meter-container">
          <img
            src={meter}
            alt="Calorie Meter"
            className="calorie-expenditure-meter"
          />
        </Grid>
        <Grid
          item
          xs
          container
          direction="column"
          justify="space-around"
          alignItems="flex-start"
        >
          <Typography
            variant={BMI < 18.5 ? "h4" : "h6"}
            className={`calorie-expenditure-meter-calories calorie-expenditure-top ${
              BMI < 18.5 && "calorie-expenditure-color-under"
            }`}
          >
            {caloriesToMaintain + 500}
            <span className="calorie-expenditure-meter-cal"> Cal</span>
          </Typography>
          <Typography
            variant={BMI >= 18.5 && BMI <= 25 ? "h4" : "h6"}
            className={`calorie-expenditure-meter-calories calorie-expenditure-middle ${
              BMI >= 18.5 && BMI <= 25 && "calorie-expenditure-color-normal"
            }`}
          >
            {caloriesToMaintain}
            <span className="calorie-expenditure-meter-cal"> Cal</span>
          </Typography>
          <Typography
            variant={BMI > 25 ? "h4" : "h6"}
            className={`calorie-expenditure-meter-calories calorie-expenditure-bottom ${
              BMI > 25 && "calorie-expenditure-color-over"
            }`}
          >
            {caloriesToMaintain - 500}
            <span className="calorie-expenditure-meter-cal"> Cal</span>
          </Typography>
        </Grid>
      </Grid>
    </>
  );
}
